*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: white;
}

.KanbanTask {
    background-color: white;
    margin: 1rem 0;
    padding: 10px;
    height: -webkit-min-content;
    height: min-content;
    min-width: 150px;
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-evenly;
    border-radius: 5px;
    opacity: 0.9;
    text-align: left;
}

.KanbanTask-content {
    padding: 1rem 0;
    font-size: 1.2rem;
}

.KanbanTask-options {
    font-size: 1.2rem;
    display: flex;
    justify-content: space-around;
    width: 100%;
}

.KanbanTask-btn-edit,
.KanbanTask-btn-delete {
    font-size: 1.2rem;
    opacity: 0.8;
    padding: 0.2rem 0.4rem;
    border: 1px solid white;
    border-radius: 10px;
    cursor: pointer;
}

.KanbanEditForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.KanbanEditForm-input {
    width: 100%;
}

.KanbanEditForm-btn {
    font-size: 1.2rem;
    opacity: 0.8;
    padding: 0.2rem 0.4rem;
    border: 1px solid white;
    border-radius: 10px;
    cursor: pointer;
    color: white;
    margin-top: 5px;
}

.KanbanColumn {
    width: 30%;
    max-width: 600px;
    text-align: center;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
    min-height: 700px;
}

.KanbanColumns-tasks-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    flex-grow: 1;
}

.KanbanColumn-name {
    color: white;
    font-size: 1.5rem;
    margin-top: 1rem;
    letter-spacing: 0.1rem;
}

.KanbanColumn-numbers {
    color: white;
    font-size: 1.2rem;
}

.KanbanColumn-footer {
    padding: 1rem 0;
}

.KanbanColumn-btn-add {
    background-color: white;
    opacity: 0.8;
    padding: 0.3rem;
    border: 1px solid white;
    border-radius: 10px;
    cursor: pointer;
}

.KanbanColumn-btn-add:hover {
    opacity: 1;
}

.Kanban {
    height: -webkit-min-content;
    height: min-content;
    width: 90vw;
    padding: 1rem 2rem;
}

.Kanban-columns-container {
    min-height: -webkit-min-content;
    min-height: min-content;
    height: 100%;
    margin: 2rem 0;
    display: flex;
    justify-content: space-evenly;
    align-items: stretch;
}

.KanbanModal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    z-index: 9;
}

.KanbanModal-content {
    background: white;
    border-radius: 10px;
    width: 35%;
    height: 700px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: auto;
}

.KanbanModal-close-btn:before {
    content: "x";
    color: black;
    font-weight: 300;
    font-family: Arial, sans-serif;
    position: absolute;
    right: 20px;
    top: 10px;
    cursor: pointer;
}

.KanbanModal-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 480px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.KanbanModal-input-container {
    margin: 10px 0;
}

.KanbanModal-input {
    width: 200px;
}

.KanbanModal-input-submit-btn {
    background-color: lightsteelblue;
    opacity: 0.8;
    width: 100px;
    padding: 0.3rem;
    border: 1px solid white;
    border-radius: 10px;
    cursor: pointer;
    justify-self: center;
}

.KanbanSidebar {
    background-color: #28314b;
    min-height: 100vh;
    max-height: 110vh;
    width: 70px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 0;
    align-self: stretch;
}

.KanbanSidebar-bottomColumn {
    height: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.KanbanPopup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    z-index: 9;
}

.KanbanPopup-content {
    background: white;
    border-radius: 10px;
    width: 15%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: auto;
}

.KanbanPopup-close-btn:before {
    content: "x";
    color: black;
    font-weight: 300;
    font-family: Arial, sans-serif;
    position: absolute;
    right: 20px;
    top: 10px;
    cursor: pointer;
}

.KanbanPopup-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 220px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.KanbanPopup-input-container {
    margin: 10px 0;
}

.KanbanPopup-input {
    width: 200px;
}

.KanbanPopup-input-submit-btn {
    background-color: lightsteelblue;
    opacity: 0.8;
    width: 100px;
    padding: 0.3rem;
    border: 1px solid white;
    border-radius: 10px;
    cursor: pointer;
    justify-self: center;
}

.App {
    display: flex;
    align-items: center;
    height: -webkit-min-content;
    height: min-content;
}

